<template>
    <div class="container">
        <h2>floatLayout 浮动弹层组件</h2>
        <h3>浮动弹层组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'button',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'title',
                    desc: '标题',
                    type: 'String',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'scrollX',
                    desc: '组件是否有横向滚动条',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                },
                {
                    param: 'isOpened',
                    desc: '组件是否显示',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                }
            ],
            events: [
                {
                    name: 'onClose',
                    desc: `组件关闭时触发的方法`,
                    param: `-`
                },
                {
                    name: 'onScroll',
                    desc: `组件滚动时触发的方法`,
                    param: `-`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiFloatLayout,
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {
        constructor(props){
            super(props)
            this.state = {
                show:false
            }
        }

        onCloseTest(){
            console.log('关闭后执行')
        }

        onShow(){
            this.setState({
                show:true
            })
        }

        render () {
          const { show } =  this.state
          return (
              <View className='index'>
                  <View onClick={()=>this.onShow()}>弹出</View>
                  <WiFloatLayout
                      isOpened={show}
                      title='标题'
                      onClose={this.onCloseTest.bind(this)}
                  >
                      <View>内容</View>
                  </WiFloatLayout>
              </View>
          )
        }
    }
<\/script>`
        }
    },
}
</script>

